import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { ImageViewerController } from 'ionic-img-viewer';

/**
 * Generated class for the StripsjointPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-stripsjoint',
  templateUrl: 'stripsjoint.html',
})
export class StripsjointPage {

  
  @ViewChild('mySlider') mySlider: any;
_imageViewerCtrl: ImageViewerController;
  constructor(public navCtrl: NavController, public navParams: NavParams, imageViewerCtrl: ImageViewerController, public platform: Platform) {
  this._imageViewerCtrl = imageViewerCtrl;
  
   let backAction =  platform.registerBackButtonAction(() => {
        console.log("contact");
        this.navCtrl.pop();
        backAction();
      },2)
  
  }
  
   presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }
  
  elenco =  [
             {
               "img": "assets/img/qaprofil/84r/1.png",
               "posicao": "84R and Joints Strips ",
               "nome": "QA8 / QAJ"
               
             },
             {
               "img": "assets/img/qaprofil/84r/2.png",
                "posicao": "84R and Joints Strips ",
               "nome": "QA8 / QAJ"
               
             },
             {
               "img": "assets/img/qaprofil/84r/3.png",
                "posicao": "84R and Joints Strips ",
               "nome": "QA8 / QAJ"
               
             },
             
             {
               "img": "assets/img/qaprofil/full_image/84r-1.png",
                "posicao": "84R and Joints Strips ",
               "nome": "QA8 / QAJ"
             } 
             
           ]
           
           
           slideNext(){
       this.mySlider.slideNext();
     }
 
     slidePrev(){
       this.mySlider.slidePrev();
     }


 




  ionViewDidLoad() {
    console.log('ionViewDidLoad StripsjointPage');
  }
  
  
  
  
  
   

}



